// Courses Section Variables
$course-primary: #008080;
$course-secondary: #667eea;
$course-accent: #764ba2;
$course-dark: #2d3748;
$course-light: #f8f9fa;
$course-text: #4a5568;
$course-border: #e2e8f0;

// Courses Learning Section
.courses-learning-section {
  padding: 5rem 0;
  background: $white;
  position: relative;
  overflow: hidden;

  .section-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    z-index: 0;

    .bg-wave {
      position: absolute;
      background: linear-gradient(90deg, transparent, rgba($course-primary, 0.05), transparent);

      &.wave-1 {
        width: 200%;
        height: 100px;
        top: 20%;
        left: -50%;
        transform: rotate(-5deg);
        opacity: 0.3;
      }

      &.wave-2 {
        width: 200%;
        height: 80px;
        bottom: 15%;
        left: -50%;
        transform: rotate(5deg);
        opacity: 0.2;
      }
    }
  }

  .container {
    position: relative;
    z-index: 2;
  }
}

// Section Header
.section-header {
  text-align: center;
  margin-bottom: 4rem;

  .section-badge {
    span {
      display: inline-block;
      background: rgba($course-primary, 0.1);
      color: $course-primary;
      padding: 8px 20px;
      border-radius: 50px;
      font-size: 0.9rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.5px;
    }
  }

  .section-title {
    font-size: 3rem;
    font-weight: 700;
    color: $course-dark;
    margin-bottom: 1.5rem;
    line-height: 1.2;

    .text-accent {
      color: $course-primary;
      background: linear-gradient(135deg, $course-primary, $course-secondary);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
    }
  }

  .section-description {
    font-size: 1.25rem;
    color: $course-text;
    line-height: 1.6;
    max-width: 700px;
    margin: 0 auto;
  }
}


// Course Categories
.course-categories {
  margin-bottom: 4rem;

  .categories-title {
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    color: $course-dark;
    margin-bottom: 2rem;
  }

  .categories-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;

    .category-card {
      background: white;
      padding: 2rem;
      border-radius: 15px;
      box-shadow: 0 5px 25px rgba(0, 0, 0, 0.08);
      border: 1px solid $course-border;
      transition: all 0.3s ease;
      position: relative;

      &:hover {
        transform: translateY(-5px);
        box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
      }

      .category-header {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 1.5rem;

        .category-icon {
          width: 60px;
          height: 60px;
          background: linear-gradient(135deg, rgba($course-primary, 0.1) 0%, rgba($course-secondary, 0.1) 100%);
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;

          i {
            font-size: 1.5rem;
            color: $course-primary;
          }
        }

        .level-badge {
          padding: 6px 12px;
          border-radius: 12px;
          font-size: 0.75rem;
          font-weight: 600;
          text-transform: uppercase;

          &.beginner {
            background: rgba(#10b981, 0.1);
            color: #10b981;
          }

          &.intermediate {
            background: rgba($course-primary, 0.1);
            color: $course-primary;
          }

          &.advanced {
            background: rgba(#f59e0b, 0.1);
            color: #f59e0b;
          }
        }
      }

      h4 {
        font-size: 1.25rem;
        font-weight: 700;
        color: $course-dark;
        margin-bottom: 1rem;
      }

      p {
        color: $course-text;
        line-height: 1.5;
        margin-bottom: 1.5rem;
      }

      .category-meta {
        display: flex;
        gap: 1rem;
        font-size: 0.9rem;

        .duration,
        .lessons {
          color: $course-primary;
          font-weight: 600;
        }
      }
    }
  }
}

// Courses CTA
.courses-cta {
  background: linear-gradient(135deg, $course-primary, $course-secondary);
  color: white;
  padding: 3rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 20px 60px rgba($course-primary, 0.3);

  .cta-content {
    margin-bottom: 2rem;

    h3 {
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 1rem;
    }

    p {
      font-size: 1.1rem;
      opacity: 0.9;
      max-width: 500px;
      margin: 0 auto;
    }
  }

  .cta-actions {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;

    .btn-primary,
    .btn-secondary {
      display: inline-flex;
      align-items: center;
      gap: 10px;
      padding: 15px 30px;
      border-radius: 50px;
      text-decoration: none;
      font-weight: 600;
      font-size: 1rem;
      transition: all 0.3s ease;
      border: 2px solid transparent;
    }

    .btn-primary {
      background: white;
      color: $course-primary;

      &:hover {
        background: rgba(white, 0.9);
        transform: translateY(-2px);
        box-shadow: 0 8px 25px rgba(white, 0.3);
      }
    }

    .btn-secondary {
      background: transparent;
      color: white;
      border-color: rgba(white, 0.3);

      &:hover {
        background: rgba(white, 0.1);
        transform: translateY(-2px);
      }
    }
  }
}

// Responsive Design
@media (max-width: 1024px) {
  .section-header .section-title {
    font-size: 2.5rem;
  }

  .journey-step {
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .courses-learning-section {
    padding: 3rem 0;
  }

  .section-header {
    margin-bottom: 3rem;

    .section-title {
      font-size: 2rem;
    }

    .section-description {
      font-size: 1.1rem;
    }
  }

  .journey-step {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 1.5rem;

    .step-visual {
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 1rem;

      .step-connector {
        width: 80px;
        height: 3px;
        margin: 0;
      }
    }

    &.featured .step-content {
      transform: none;
    }
  }

  .features-grid,
  .categories-grid {
    grid-template-columns: 1fr;
  }

  .courses-cta {
    padding: 2rem;

    .cta-content h3 {
      font-size: 1.5rem;
    }

    .cta-actions {
      flex-direction: column;
      align-items: center;

      .btn-primary,
      .btn-secondary {
        width: 100%;
        max-width: 250px;
        justify-content: center;
      }
    }
  }
}

@media (max-width: 480px) {
  .section-header .section-title {
    font-size: 1.75rem;
  }

  .journey-step .step-content {
    padding: 1.5rem;
  }

  .courses-cta {
    padding: 1.5rem;
  }
}

// Animations
.journey-step {
  opacity: 0;
  transform: translateX(-30px);
  animation: slideInRight 0.6s ease forwards;
}

.journey-step:nth-child(1) { animation-delay: 0.1s; }
.journey-step:nth-child(2) { animation-delay: 0.3s; }
.journey-step:nth-child(3) { animation-delay: 0.5s; }

.feature-card,
.category-card {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease forwards;
}

.feature-card:nth-child(1) { animation-delay: 0.2s; }
.feature-card:nth-child(2) { animation-delay: 0.4s; }
.feature-card:nth-child(3) { animation-delay: 0.6s; }
.feature-card:nth-child(4) { animation-delay: 0.8s; }

.category-card:nth-child(1) { animation-delay: 0.3s; }
.category-card:nth-child(2) { animation-delay: 0.5s; }
.category-card:nth-child(3) { animation-delay: 0.7s; }

@keyframes slideInRight {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
